body {
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: 'Oswald', sans-serif;
  font-size: 20px;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.CounterParent{
  height: 50%;
  width: 50%;
  margin: auto;
  padding-top: 5%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.Countdown ul{
  height: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  list-style: none;
  margin-bottom: 0px;
}


.CountSetter{
  border: 3px solid black;
  border-radius: 20px;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction:column;
          flex-direction:column;
  -webkit-justify-content: center;
          justify-content: center;
}

.CountSetterForm, .Countdown{
  background: linear-gradient(to bottom right, LightSeaGreen, #52e0d9, Transparent);
}

.CountSetterForm{
  border-radius: 20px 20px 0px 0px;
}

.CountSetterForm ul{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  padding-left: 0px;
}

.CountSetterForm li{
  margin: auto;
  list-style: none;
  padding: 15px;
}

.CountSetterForm input{
  width: 50px;
  height: 30px;
  font-size: 20px;
  padding: 5px;
  margin-left: 10px;
}

.SetButton{
  margin: auto;
}

.CountFormatterBox{
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}

.CountFormatterForm{
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

.CountFormatterForm label{
  margin-right: 10px;
}

.CountFormatterForm li{
  padding:10px;
  padding-bottom: 15px;
}

.LowerBox h3{
  margin-top: 0px;
  margin-bottom: 5px;
}

.Countdown{
  border-radius: 0px 0px 20px 20px;
}

.Countdown ul{
  margin-top: 0px;
  /* margin-bottom: 5px; */
  padding:0px;
  padding-top: 15px;
}

.ButtonParent{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

.StartButton, .SetButton, .ResetButton{
  width: 180px;
  height: 30px;
  background: DodgerBlue;
  font-size: 14px;
  font-weight: bold;
  color: white;
  margin-top: 15px;
  margin-bottom: 20px;
  border-radius: 5px;
}

.StartButton{
  margin-bottom: 0px;
}

#ClickedStart{
  background: gray;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

